.input-group {
  .form-control, .input-group-addon, .input-group-btn {
    display: table-cell;
    height: 34px;
  }
  .input-group-btn .btn {
    height: 34px;
  }
}
.bootstrap-tagsinput {
  width: 100%;
  box-shadow: none;
  clear: both;
  &:before,
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}